import classNames from 'classnames';
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Button from './Button';

function PizzaCard({ imageUrl, price, name, types, sizes, onClickPizzaToCart, id, countPizza }) {
  //Обьявляем наш псевдо state
  //TODO потом его закиним в state react redux
  const avialibleTypesPizza = ['тонкое', 'традиционное'];
  const avialibleSizePizza = [26, 30, 40];

  //Доступные типы и размеры пицц, пришедшие с сервера
  const [activeType, setactiveType] = useState(types[0]);
  const [aviliableSize, setAviliableSize] = useState(sizes[0]);

  //Меняем тип при клике
  const onChangeTypes = index => {
    setactiveType(index);
  };

  //Меняем размер при клике
  const onChangeSize = type => {
    setAviliableSize(type);
  };

  const addPizza = () => {
    onClickPizzaToCart({
      id,
      name,
      price,
      imageUrl,
      types: avialibleTypesPizza[activeType],
      sizes: aviliableSize,
    });
  };

  return (
    <div className='pizza-block'>
      <img className='pizza-block__image' src={imageUrl} alt='Pizza' />
      <h4 className='pizza-block__title'>{name}</h4>
      <div className='pizza-block__selector'>
        <ul>
          {avialibleTypesPizza.map((type, index) => {
            return (
              <li
                key={type}
                onClick={() => onChangeTypes(index)}
                className={classNames({
                  active: activeType === index,
                  disabled: !types.includes(index),
                })}>
                {type}
              </li>
            );
          })}
        </ul>
        <ul>
          {avialibleSizePizza.map((type, index) => {
            return (
              <li
                key={type}
                onClick={() => onChangeSize(type)}
                className={classNames({
                  active: aviliableSize === type,
                  disabled: !sizes.includes(type),
                })}>
                {type} см.
              </li>
            );
          })}
        </ul>
      </div>
      <div className='pizza-block__bottom'>
        <div className='pizza-block__price'>от {price} ₽</div>
        <Button oncClicck={addPizza} className='button--add' outline>
          <svg
            width='12'
            height='12'
            viewBox='0 0 12 12'
            fill='none'
            xmlns='http://www.w3.org/2000/svg'>
            <path
              d='M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z'
              fill='white'
            />
          </svg>
          <span>Добавить</span>
          {countPizza && <i>{countPizza}</i>}
        </Button>
      </div>
    </div>
  );
}

//Задаем строгую типизация через PropTypes
PizzaCard.propTypes = {
  name: PropTypes.string.isRequired,
  price: PropTypes.number.isRequired,
  imageUrl: PropTypes.string.isRequired,
  sizes: PropTypes.arrayOf(PropTypes.number).isRequired,
  types: PropTypes.arrayOf(PropTypes.number).isRequired,
};

//Значения по умолчанию, если не были переданы нужные свойстава или методы в props
PizzaCard.defaultProps = {
  name: 'Пицца',
  price: 0,
  types: [],
  sizes: [],
};

export default PizzaCard;
